/*=================================
=            Variables            =
=================================*/
/*==========  Colors  ==========*/
/*==========  General vars  ==========*/
/*==========  Mixins  ==========*/
/*==========  Fonts  ==========*/
/*==========  z-index index  ==========*/
/*==========  Spatiere  ==========*/
.strip_ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
/*======================================
=            General styles            =
======================================*/
html,
body {
  font-size: 16px;
}
body {
  color: #333333;
  font-family: 'Roboto', sans-serif;
}
/*==========  Typo  ==========*/
p {
  margin-bottom: 20px;
  line-height: 1.4;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
blockquote h2 {
  margin-bottom: 0;
}
::selection {
  background: #ffb7b7;
  /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #ffb7b7;
  /* Gecko Browsers */
}
/*==========  Form  ==========*/
/*==========  Other Elements  ==========*/
img {
  max-width: 100%;
}
/*==================================
=            Components            =
==================================*/
/*==========  Small Components  ==========*/
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.list li {
  position: relative;
  margin-bottom: 6px;
}
.list li:before {
  position: absolute;
  left: 0;
  top: -1px;
  content: '\203A';
  display: inline-block;
  margin-right: 5px;
}
.list a:hover {
  text-decoration: underline;
}
/*====================================
=            Pagina Lista            =
====================================*/
.pagination,
.wp-pagenavi {
  padding: 28px 20px  50px 28px;
}
.pagination p,
.wp-pagenavi p {
  margin-bottom: 0;
}
.pagination a,
.pagination span,
.wp-pagenavi a,
.wp-pagenavi span {
  display: inline-block;
  margin-right: 10px;
  width: 36px;
  height: 34px;
  line-height: 29px;
  border-radius: 3px;
  background: #fff;
  font-weight: 700;
  font-size: 13px;
  text-align: center;
}
.pagination a.current,
.pagination span.current,
.wp-pagenavi a.current,
.wp-pagenavi span.current {
  color: #fff;
}
.pagination a:hover,
.pagination span:hover,
.wp-pagenavi a:hover,
.wp-pagenavi span:hover {
  text-decoration: none;
}
/*==========  Featured Image  ==========*/
.featured-image {
  margin-bottom: 20px;
}
